import  DashboardCard from './components/DashboardCards';

export default function TMSDashboard() {
  return (
    <div className="erp-dashboard">
      <div className="cards-container">
        <DashboardCard title="HR Manager" action="View Details" />
        <DashboardCard title="Payroll" action="View Details" />
        <DashboardCard title="Procurement" action="View Details" />
        <DashboardCard title="Lookup" action="View Details" />
      </div>

      <div className="chart-section">
        <h2>Area Chart</h2>
        <table>
          <thead>
            <tr>
              {['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'].map(month => (
                <th key={month}>{month}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>14</td>
              {Array(11).fill('').map((_, i) => (
                <td key={i}></td>
              ))}
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}